














































  import vueagreement from './agreement.vue'
  
	export default {
    props: {
      contactData: {
        type: Object,
        required: true
      }
    },
    components: {
      vueagreement
    },
		data() {
			return {
				isShow: false,
        type: '',
        productId: '',
        link: '',
        lists: [], // 申请人列表
        agree: true, // 是否同意协议
        chooseIndex: -1, // 当前选择申请人
        deleteIndex: 0,
        deleting: false,
        submiting: false,
        agreeData: {},
        isCardApply: false
			};
		},
    computed: {
      config() {
        return this.$store.state.config
      },
      disabled() {
        return this.lists.length <= 0 || !this.agree || this.chooseIndex === -1
      }
    },
    methods: {
      hide() {
      	this.isShow = false
      	this.$emit('close')
      },
      addContact() {
        this.hide()
        this.$emit('add')
      },
      choose(n) {
        if (this.chooseIndex === n) {
          this.chooseIndex = -1
        } else {
          this.chooseIndex = n
        }
      },
      checkAgree() {
        this.agree = !this.agree
      },
      showAgree() {
        this.agreeData = {
          show: true
        }
      },
      getLIsts(b) {
        this.$http({
        	url: this.$apis.contactList,
        	method: 'GET',
        	data: null,
        	cb: (err, data) => {
        		if (!err && data.success && data.data.data.length > 0) {
        			this.lists = data.data.data
        		} else {
              this.addContact()
        		}
        	}
        })
      },
      deletePop(n) {
        this.deleteIndex = n
        uni.showModal({
        	title: '提示',
          content: `'您确定要删除申请人 ${this.lists[this.deleteIndex].name} 吗？'`,
          success: (res) => {
          	if (res.confirm) {
              if (this.deleting) return
              this.deleting = true
              this.$http({
                url: this.$apis.contactDelete,
                method: 'DELETE',
                data: {
                  id: this.lists[this.deleteIndex].id
                },
                cb: (err, data) => {
                  if (!err && data.success) {
                    this.getLIsts(false)
                  } else {
                    uni.showToast({
                    	title: data.message ? data.message : '删除成功',
                      icon: 'none'
                    })
                  }
                  this.deleting = false
                }
              })
            }
          }
        })
      },
      checkInfo() {
        if (this.disabled || this.submiting) return
        this.submit()
      },
      submit() {
        if (this.submiting || !this.type) return
        this.submiting = true
        switch (this.type) {
          case 'xyk':
            if (this.isCardApply) {
              this.$http({
                url: this.$apis.createContact,
                method: 'POST',
                data: {
                  id: this.lists[this.chooseIndex].id,
                  card_id: this.productId
                },
                cb: (err, data) => {
                  if (!err && data.success) {
                    this.hide()
                    this.submiting = false
                    this.$emit('success')
                    setTimeout(() => {
                      uni.setStorageSync('webSrc', data.data.apply_link)
                      this.$navigateTo({
                      	url: '/pages/web/index'
                      })
                    }, 50)
                  } else {
                    uni.showToast({
                    	title: data && data.message ? data.message : '申请失败',
                      icon: 'none'
                    })
                    this.submiting = false
                  }
                }
              })
            } else {
              this.$http({
                url: this.$apis.createContact,
                method: 'POST',
                data: {
                  id: this.lists[this.chooseIndex].id,
                  bank_id: this.productId
                },
                cb: (err, data) => {
                  if (!err && data.success) {
                    this.hide()
                    this.submiting = false
                    this.$emit('success')
                    setTimeout(() => {
                      uni.setStorageSync('webSrc', data.data.apply_link)
                    	this.$navigateTo({
                    		url: '/pages/web/index'
                    	})
                    }, 50)
                  } else {
                    uni.showToast({
                      title: data && data.message ? data.message : '申请失败',
                      icon: 'none'
                    })
                    this.submiting = false
                  }
                }
              })
            }
            break
          case 'bx':
            this.$http({
              url: this.$apis.insuranceOrder,
              method: 'POST',
              data: {
                contact_id: this.lists[this.chooseIndex].id,
                id: this.productId
              },
              cb: (err, data) => {
                if (!err && data.success) {
                  this.hide()
                  this.submiting = false
                  this.$emit('success')
                  setTimeout(() => {
                    uni.setStorageSync('webSrc', this.link)
                  	this.$navigateTo({
                  		url: '/pages/web/index'
                  	})
                  }, 50)
                } else {
                  uni.showToast({
                  	title: data && data.message ? data.message : '申请失败',
                  	icon: 'none'
                  })
                  this.submiting = false
                }
              }
            })
            break
          case 'dk':
            this.$http({
              url: this.$apis.loanOrder,
              method: 'POST',
              data: {
                contact_id: this.lists[this.chooseIndex].id,
                id: this.productId
              },
              cb: (err, data) => {
                if (!err && data.success) {
                  this.hide()
                  this.submiting = false
                  this.$emit('success')
                  setTimeout(() => {
                    uni.setStorageSync('webSrc', this.link)
                  	this.$navigateTo({
                  		url: '/pages/web/index' 
                  	})
                  }, 50)
                } else {
                  uni.showToast({
                    title: data && data.message ? data.message : '申请失败',
                    icon: 'none'
                  })
                  this.submiting = false
                }
              }
            })
            break
        }
      }
    },
    watch: {
      contactData: function (val) {
        this.isShow = val.show
        this.type = val.type
        this.productId = val.productId
        this.link = val.link
        this.chooseIndex = val.index
        this.isCardApply = val.card
        if (this.isShow) {
          this.getLIsts(true)
        }
      }
    }
	}
