

















































































	export default {
    props: {
      relationData: {
        type: Object,
        required: true
      }
    },
		data() {
			return {
				isShow: false
			};
		},
    computed: {
      config() {
        return this.$store.state.config
      }
    },
    methods: {
      hide() {
        this.isShow = false
        this.$emit('close')
      }
    },
    watch: {
      relationData(val) {
        this.isShow = val.show
      }
    }
	}
