









































































































































	export default {
    props: {
      agreeData: {
        type: Object,
        required: true
      }
    },
		data() {
			return {
				isShow: false
			};
		},
    computed: {
      config() {
        return this.$store.state.config
      }
    },
    methods: {
      confirm() {
        this.isShow = false
      }
    },
    watch: {
      agreeData: function (val) {
        this.isShow = val.show
      }
    }
	}
