





































































































  import vueagreement from './agreement.vue'
  
  const COUNTSECEND = 60
  
	export default {
		props: {
			addData: {
				type: Object,
				required: true
			}
		},
    components: {
      vueagreement
    },
		data () {
			return {
				isShow: false,
        type: '',
        productId: '',
        link: '',
        confirmState: 0,
        errorDefault: '',
        error: {
          msg: '',
          type: false
        },
        errorTimer: null,
        username: '', // 姓名
        idcard: '', // 身份证号码
        mobile: '', // 手机号码
        code: '', // 验证码
        agree: true, // 是否同意协议
        isSave: 1,
        hasSendCode: false,
        isClickCode: false,
        sending: false,
        countNum: COUNTSECEND,
        countTimer: null,
        agreeData: {},
        submiting: false, // 是否在提交中
        blankShow: false,
        clearShow: [],
        clearTimer: null,
        isCardApply: false
			}
		},
    computed: {
    	config() {
    		return this.$store.state.config
    	}
    },
    onShow () {
      // 是否处于验证码获取倒计时状态
      if (uni.getStorageInfoSync('addCodeStamp')) {
        let oldtime = uni.getStorageInfoSync('addCodeStamp')
        let nowtime = new Date().getTime()
        let cha = parseInt((nowtime - parseInt(oldtime)) / 1000)
        if (cha < COUNTSECEND) {
          this.sending = true
          this.countNum = COUNTSECEND - cha
          this.countTimer = setInterval(() => {
            this.countNum --
            if (this.countNum <= 0) {
              clearInterval(this.countTimer)
              this.countNum = COUNTSECEND
              this.sending = false
              this.isClickCode = false
            }
          }, 1000)
          this.hasSendCode = true
        }
      }
    },
		methods: {
			hide() {
        this.isShow = false
        this.$emit('close')
      },
      callPhone(num) {
        uni.makePhoneCall({
        	phoneNumber: num
        })
      },
      checkAgree() {
        this.agree = !this.agree
      },
      showAgree() {
        this.agreeData = {
          show: true
        }
      },
      checkSave() {
        if (this.isSave === 0) {
          this.isSave = 1
        } else {
          this.isSave = 0
        }
      },
      sendCode() {
        if (this.isClickCode) return
        if (!this.$utils.validatePhone(this.mobile)) {
          this.error = {
            msg: '请输入正确的手机号码',
            type: true
          }
          clearTimeout(this.errorTimer)
          this.errorTimer = setTimeout(() => {
            this.error = {
              msg: this.errorDefault,
              type: false
            }
          }, 2000)
        } else {
          this.isClickCode = true
          if (!this.hasSendCode) this.hasSendCode = true
          this.$http({
            url: this.$apis.phone,
            method: 'POST',
            data: {
              'type': 3,
              'mobile': this.mobile
            },
            cb: (err, data) => {
              if (!err && data.success) {
              	this.sending = true
              	this.countTimer = setInterval(() => {
              		this.countNum --
              		if (this.countNum <= 0) {
              			clearInterval(this.countTimer)
              			this.countNum = COUNTSECEND
              			this.sending = false
              			this.isClickCode = false
              		}
              	}, 1000)
              	// 保存发送时间
                uni.setStorageSync('addCodeStamp', (new Date().getTime()).toString())
              } else {
              	this.error = {
              		msg: data.message,
              		type: true
              	}
              	clearTimeout(this.errorTimer)
              	this.errorTimer = setTimeout(() => {
              		this.error = {
              			msg: this.errorDefault,
              			type: false
              		}
              	}, 2000)
              	this.sending = false
              	this.isClickCode = false
              }
            }
          })
        }
      },
      confirm() {
        if (!this.$utils.validateName(this.username)) {
          this.error = {
            msg: '请输入正确的姓名',
            type: true
          }
          clearTimeout(this.errorTimer)
          this.errorTimer = setTimeout(() => {
            this.error = {
              msg: this.errorDefault,
              type: false
            }
          }, 2000)
        } else if (!this.$utils.validateIdCard(this.idcard)) {
          this.error = {
            msg: '请输入正确的身份证号码',
            type: true
          }
          clearTimeout(this.errorTimer)
          this.errorTimer = setTimeout(() => {
            this.error = {
              msg: this.errorDefault,
              type: false
            }
          }, 2000)
        } else if (!this.$utils.validatePhone(this.mobile)) {
          this.error = {
            msg: '请输入正确的手机号码',
            type: true
          }
          clearTimeout(this.errorTimer)
          this.errorTimer = setTimeout(() => {
            this.error = {
              msg: this.errorDefault,
              type: false
            }
          }, 2000)
        } else if (!this.hasSendCode) {
          this.error = {
            msg: '验证码已失效，请重新发送',
            type: true
          }
          clearTimeout(this.errorTimer)
          this.errorTimer = setTimeout(() => {
            this.error = {
              msg: this.errorDefault,
              type: false
            }
          }, 2000)
        } else if (this.code.length <= 0) {
          this.error = {
            msg: '请输入验证码',
            type: true
          }
          clearTimeout(this.errorTimer)
          this.errorTimer = setTimeout(() => {
            this.error = {
              msg: this.errorDefault,
              type: false
            }
          }, 2000)
        } else if (!this.agree) {
          this.error = {
            msg: '请阅读并勾选《' + this.$store.state.config.system_nick_name + '用户协议》',
            type: true
          }
          clearTimeout(this.errorTimer)
          this.errorTimer = setTimeout(() => {
            this.error = {
              msg: this.errorDefault,
              type: false
            }
          }, 2000)
        } else {
          if (this.submiting) return
          this.confirmState = 1
        }
      },
      submit() {
        if (this.submiting) return
        this.submiting = true
        switch (this.type) {
          case 'xyk':
            if (this.isCardApply) {
              this.$http({
                url: this.$apis.create,
                method: 'POST',
                data: {
                  name: this.username,
                  id_card: this.idcard,
                  mobile: this.mobile,
                  verify_code: this.code,
                  card_id: this.productId,
                  is_save: this.isSave
                },
                cb: (err, data) => {
                  if (!err && data.success) {
                    uni.removeStorageSync('addCodeStamp')
                  	this.hide()
                  	this.submiting = false
                  	this.$emit('success')
                    setTimeout(() => {
                      uni.setStorageSync('webSrc', data.data.apply_link)
                    	this.$navigateTo({
                    		url: '/pages/web/index'
                    	})
                    }, 50)
                  } else {
                  	this.error = {
                  		msg: data.message ? data.message : '申请失败',
                  		type: true
                  	}
                  	clearTimeout(this.errorTimer)
                  	this.errorTimer = setTimeout(() => {
                  		this.error = {
                  			msg: this.errorDefault,
                  			type: false
                  		}
                  	}, 2000)
                  	this.code = ''
                  	this.submiting = false
                  	this.confirmState = 0
                  }
                }
              })
            } else {
              this.$http({
                url: this.$apis.create,
                method: 'POST',
                data: {
                  name: this.username,
                  id_card: this.idcard,
                  mobile: this.mobile,
                  verify_code: this.code,
                  bank_id: this.productId,
                  is_save: this.isSave
                },
                cb: (err, data) => {
                  if (!err && data.success) {
                    uni.removeStorageSync('addCodeStamp')
                    this.hide()
                    this.submiting = false
                    this.$emit('success')
                    setTimeout(() => {
                      uni.setStorageSync('webSrc', data.data.apply_link)
                    	this.$navigateTo({
                    		url: '/pages/web/index'
                    	})
                    }, 50)
                  } else {
                    this.error = {
                      msg: data.message ? data.message : '申请失败',
                      type: true
                    }
                    clearTimeout(this.errorTimer)
                    this.errorTimer = setTimeout(() => {
                      this.error = {
                        msg: this.errorDefault,
                        type: false
                      }
                    }, 2000)
                    this.code = ''
                    this.submiting = false
                    this.confirmState = 0
                  }
                }
              })
            }
            break
          case 'bx':
            this.$http({
              url: this.$apis.insuranceOrder,
              method: 'POST',
              data: {
                name: this.username,
                id_card: this.idcard,
                mobile: this.mobile,
                verify_code: this.code,
                id: this.productId,
                is_save: this.isSave
              },
              cb: (err, data) => {
                if (!err && data.success) {
                	uni.removeStorageSync('addCodeStamp')
                	this.hide()
                	this.submiting = false
                	this.$emit('success')
                  setTimeout(() => {
                    uni.setStorageSync('webSrc', this.link ? this.link : data.data.apply_link)
                  	this.$navigateTo({
                  		url: '/pages/web/index'
                  	})
                  }, 50)
                } else {
                	this.error = {
                		msg: data.message ? data.message : '申请失败',
                		type: true
                	}
                	clearTimeout(this.errorTimer)
                	this.errorTimer = setTimeout(() => {
                		this.error = {
                			msg: this.errorDefault,
                			type: false
                		}
                	}, 2000)
                	this.code = ''
                	this.submiting = false
                	this.confirmState = 0
                }
              }
            })
            break
          case 'dk':
            this.$http({
              url: this.$apis.loanOrder,
              method: 'POST',
              data: {
                name: this.username,
                id_card: this.idcard,
                mobile: this.mobile,
                verify_code: this.code,
                id: this.productId,
                is_save: this.isSave
              },
              cb: (err, data) => {
                if (!err && data.success) {
                	uni.removeStorageSync('addCodeStamp')
                	this.hide()
                	this.submiting = false
                	this.$emit('success')
                	setTimeout(() => {
                    uni.setStorageSync('webSrc', this.link ? this.link : data.data.apply_link)
                		this.$navigateTo({
                			url: '/pages/web/index'
                		})
                	}, 50)
                } else {
                	this.error = {
                		msg: data.message ? data.message : '申请失败',
                		type: true
                	}
                	clearTimeout(this.errorTimer)
                	this.errorTimer = setTimeout(() => {
                		this.error = {
                			msg: this.errorDefault,
                			type: false
                		}
                	}, 2000)
                	this.code = ''
                	this.submiting = false
                	this.confirmState = 0
                }
              }
            })
            break
        }
      }
		},
		watch: {
			addData: function (val) {
				this.isShow = val.show
        this.type = val.type
        this.confirmState = val.state
        this.productId = val.productId
        this.link = val.link
        this.username = ''
        this.idcard = ''
        this.mobile = ''
        this.code = ''
        this.agree = true
        this.isSave = 1
        this.isCardApply = val.card
        switch (this.type) {
          case 'xyk':
            this.errorDefault = '请填写申请人信息(必须与信用卡申请列表所填信息一致)'
            break
          case 'bx':
            this.errorDefault = '请填写投保人信息(必须与保险申请列表所填信息一致)'
            break
          case 'dk':
            this.errorDefault = '请填写贷款人信息(必须与贷款申请列表所填信息一致)'
            break
        }
        this.error = {
          msg: this.errorDefault,
          type: false
        }
			},
      idcard: function (val) {
        this.idcard = val.replace(/x/g, 'X')
      }
		}
	}
